import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {  Form, Input, Select, Icon, Button, Row, Col, Card } from 'antd';


import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';

import { EditorState, convertToRaw, ContentState } from 'draft-js';
import { Editor } from 'react-draft-wysiwyg';
import draftToHtml from 'draftjs-to-html';
import htmlToDraft from 'html-to-draftjs';

const Option = Select.Option;
const FormItem = Form.Item;
const createForm = Form.create;

export class TermForm extends Component {

  static propTypes = {
    formValues: PropTypes.object,
    form: PropTypes.object,
    termlist: PropTypes.array,
    handleOK: PropTypes.func
  }

  constructor(props) {
    super(props);
    const html = (props.formValues && props.formValues.post_content) ? props.formValues.post_content : '';
    const contentBlock = htmlToDraft(html);
    if (contentBlock) {
        const contentState = ContentState.createFromBlockArray(contentBlock.contentBlocks);
        const editorState = EditorState.createWithContent(contentState);
        this.state = {
            editorState,
        };
    }
  }

  componentWillReceiveProps (props) {
    if ( props.formValues && this.props.formValues._id !== props.formValues._id ) {
      const html = (props.formValues && props.formValues.post_content) ? props.formValues.post_content : '';
      const contentBlock = htmlToDraft(html);
      if (contentBlock) {
        const contentState = ContentState.createFromBlockArray(contentBlock.contentBlocks);
        const editorState = EditorState.createWithContent(contentState);
        this.setState({
          editorState
        })
      }
    }
  }

  onEditorStateChange: Function = (editorState) => {
    this.setState({
        editorState,
    });
  };

  handleSubmit = (e) => {
    const form = this.props.form;
    const fields = this.props.form.getFieldsValue();
    const post_status = e.target.dataset.status;
    if (this.props.formValues && this.props.formValues._id) {
      fields._id = this.props.formValues._id;
    }
    fields.post_status = post_status;
    fields.post_content = draftToHtml(convertToRaw(this.state.editorState.getCurrentContent()));
    form.validateFields((err) => {
      if (!err) {
        this.props.handleOK(fields, (err) => {
          console.log(err);

          const errObj = {};
          for (const i in err.errors) {
            if (err.errors[i]) {
              errObj[i] = {
                value: err.errors[i].value,
                errors: [new Error(err.errors[i].message)]
              }
            }
          }
          form.setFields(errObj);
        });
      }
    });
  }

  render() {

    const { getFieldDecorator } = this.props.form;
    const {termlist} = this.props;
    const defaultSelect = termlist.length > 0 ? termlist[0]._id : '';
    const domain = window.INIT_DATA.domain;
    const { editorState } = this.state;
    console.log(editorState);

    function uploadImageCallBack(file) {
        return new Promise(
            (resolve, reject) => {
                const xhr = new XMLHttpRequest();
                xhr.open('POST', '/api/resource/upload');
                const data = new FormData();
                data.append('uploadFiles', file);
                xhr.send(data);
                xhr.addEventListener('load', () => {
                    const response = JSON.parse(xhr.responseText);
                    if (response.code !== 1) {
                      reject(response)
                    } else {
                      console.log(response.data);
                      resolve({
                          data: {
                            alt: response.data.originName,
                            link: response.data.url
                          }
                      });
                    }
                });
                xhr.addEventListener('error', () => {
                    const error = JSON.parse(xhr.responseText);
                    reject(error);
                });
            }
        );
    }

    return (
      <div>
        <Form layout="horizontal" onSubmit={this.handleSubmit}>
          <Row>
            <Col span={18}>
              <FormItem
                hasFeedback
              >
                {getFieldDecorator('post_title', {
                  rules: [{ required: true, message: '文章标题不能为空!' }],
                })(
                  <Input placeholder="填写文章标题" autoComplete="off" />
                )}
              </FormItem>

              <FormItem
                hasFeedback
              >
                {getFieldDecorator('post_slug', {
                  rules: [{
                    required: true, message: '缩略名不能为空!!',
                  }],
                })(
                  <Input addonBefore={`${domain}/post`} addonAfter=".html&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;" placeholder="文章缩略名" autoComplete="off" />
                )}
              </FormItem>

              <Editor
                editorState={editorState}
                wrapperClassName="demo-wrapper"
                editorClassName="demo-editor"
                toolbar={{
                    options: ['inline', 'blockType', 'fontSize', 'fontFamily', 'list', 'textAlign', 'colorPicker', 'link', 'image'],
                    image: { uploadCallback: uploadImageCallBack, alt: { present: true, mandatory: true } }
                }}
                localization={{
                    locale: 'zh',
                }}
                onEditorStateChange={this.onEditorStateChange}
              />
            </Col>
            <Col span={6} style={{paddingLeft: '20px'}}>

              <Card title="分类管理">
                <FormItem
                  label="所属分类"
                  hasFeedback
                >
                  {getFieldDecorator('post_term', {
                    initialValue: defaultSelect,
                    rules: [
                      { required: true, message: '所属分类不能为空!' },
                    ],
                  })(
                    <Select>
                      {
                        termlist.map((val) => {
                          return <Option value={val._id} key={val._id}>{val.term_name}</Option>
                        })
                      }
                    </Select>
                  )}
                </FormItem>
              </Card>

              <Card title="发布" className="mt20">
                <Button type="primary" size="large" data-status="1" onClick={this.handleSubmit}>立即发布</Button>
                <Button type="ghost" size="large" data-status="0" onClick={this.handleSubmit} style={{marginLeft: '10px'}}>保存草稿</Button>
              </Card>

            </Col>
          </Row>
        </Form>
      </div>
    )
  }

}

export default createForm({
  mapPropsToFields(props) {

    if (!props.formValues) {
      return {}
    }
    const {post_title, post_slug, post_content, post_term} = props.formValues;
    return {
      post_title: {
        ...post_title,
        value: post_title,
      },
      post_slug: {
        ...post_slug,
        value: post_slug
      },
      post_content: {
        ...post_content,
        value: post_content
      },
      post_term: {
        ...post_term,
        value: post_term
      }
    };
  }
})(TermForm);



